@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{height:100vh;width:100vw}.background{height:100vh;width:100vw;background:linear-gradient(235deg, #4C566A, #ECEFF4);background-size:400% 400%;animation:AnimationName 10s ease infinite}.dark .background{background:linear-gradient(235deg, #4c566a, #2e3440);background-size:400% 400%}@keyframes AnimationName{0%{background-position:0% 52%}50%{background-position:100% 49%}100%{background-position:0% 52%}}
.path1{stroke-dasharray:3748.0791015625;stroke-dashoffset:3748.0791015625;stroke-width:10px;animation:draw 5s forwards}@keyframes draw{to{stroke-dashoffset:0}}.intro{display:flex;justify-content:center;align-items:center;height:100%;width:100%}.intro .center-box{background:#fff;height:25vw;width:50vw;border-radius:5vh;box-shadow:0 0 40px rgba(0,0,0,.1);display:flex;flex-direction:column;justify-content:center;align-items:center}.intro .center-box h1{font-family:"Quicksand",sans-serif;font-size:62.5px;font-weight:400}.intro .center-box button{background:#d8dee9;padding:3vh;border:none;border-radius:5vh;font-family:"Quicksand",sans-serif;font-size:3vh;margin-top:5vh}
.card{width:100%;background:#d8dee9;box-shadow:0 4px 10px rgba(0,0,0,.25);border-radius:2vmin;margin:3vmin 0;padding:2vmin}.card .cardhead{width:100%;display:flex;justify-content:space-between;align-items:center}.card .cardhead h4{text-transform:capitalize}.card .cardhead .description{width:100%;text-overflow:ellipsis}.card textarea{width:100%}
.column{width:300px;background:#e5e9f0;border-radius:3vmin;box-shadow:0 0 40px rgba(0,0,0,.1);background:#e5e9f0;padding:3vmin;height:90vh;display:flex;flex-direction:column}.column .colheader{display:flex;justify-content:space-between;height:10%;align-items:center}.column .colheader h1{text-transform:capitalize}.cards{height:90%;flex-grow:1;overflow-y:scroll}
.board{display:grid;grid-auto-flow:column;grid-gap:2vw;overflow-x:scroll;height:100vh;padding:5vh;color:#434c5e}::-webkit-scrollbar{display:none}.addcol{width:30vmin;height:30vmin;background:#e5e9f0;border-radius:3vmin;box-shadow:0 0 40px rgba(0,0,0,.1);display:flex;justify-content:center;align-items:center;font-size:15vmin;font-weight:700;color:#4c566a;place-self:center}
